<script>
    import { onMount } from 'svelte';
    import { scaleLinear, scaleBand, scaleOrdinal } from 'd3-scale';
    import { axisBottom, axisLeft } from 'd3-axis';
    import { max } from 'd3-array';
    import { rgb } from 'd3-color';
    import { transition } from 'd3-transition';
    import * as d3 from 'd3-selection';

    // values is [baseValue, adjValue]
    let data = [];
    //     { statName: 'Strength', values: [10, 20] },
    //     { statName: 'Intelligence', values: [12, 28] },
    //     { statName: 'Agility', values: [35, 40] },
    //     { statName: 'Understanding', values: [17, 31] },
    //     { statName: 'Explosiveness', values: [22, 28] },
    //     { statName: 'Luck', values: [35, 66] },
    //     { statName: 'Strength2', values: [10, 20] },
    //     { statName: 'Intelligence2', values: [12, 150] },
    //     { statName: 'Agility2', values: [9, 40] },
    //     { statName: 'Understanding2', values: [17, 31] },
    //     { statName: 'Explosiveness2', values: [22, 28] },
    //     { statName: 'Luck2', values: [35, 66] },
    //     { statName: 'Wumbo', values: [0, 40] }
    // ];

    // let hero = 'Cyphina Lyena';

    const margin = { top: 15, right: 40, bottom: 0, left: 100 };
    const width = 600 - margin.left - margin.right;
    const height = 600 - margin.top - margin.bottom;

    const xScale = scaleLinear();
    const yScale = scaleBand().padding(1);

    const xAxis = axisBottom(xScale);
    const yAxis = axisLeft(yScale).tickSize(0);

    const colors = ['#968868', '#4287f5'];
    let svg, graph, xAxisGroup, yAxisGroup;

    // Setup initial functionality
    onMount(() => {
        svg = d3
            .select('.canvas')
            .attr('class', 'svg')
            .append('svg')
            .attr('width', width + margin.left + margin.right)
            .attr('height', height + margin.top);

        //Container for the gradients
        var defs = svg.append('defs');

        //SVG Filter for the outside glow (https://apike.ca/prog_svg_filter_feMerge.html)
        var filter = defs.append('filter').attr('id', 'glow');
        filter
            .append('feGaussianBlur')
            .attr('stdDeviation', '1')
            .attr('result', 'coloredBlur'); // Result of the blur
        var feMerge = filter.append('feMerge'); // Sharp origina lelement
        feMerge.append('feMergeNode').attr('in', 'coloredBlur');
        feMerge.append('feMergeNode').attr('in', 'SourceGraphic');

        // const title = svg
        //     .append('text')
        //     .attr('x', (2 * (margin.left - margin.right) + width) / 2)
        //     .attr('y', 0 + margin.top * 2)
        //     .attr('text-anchor', 'middle')
        //     .style('fill', 'black')
        //     .style('font-size', '16px')
        //     .style('text-decoration', 'underline')
        //     .text(`${hero}'s Stats`);

        graph = svg
            .append('g')
            .attr('class', 'graph')
            .attr('transform', `translate(${margin.left},${-margin.top - 5})`);

        xAxisGroup = graph
            .append('g')
            .attr('class', 'xAxis')
            .attr('transform', `translate(0, ${height})`)
            .style('opacity', '0');
        yAxisGroup = graph.append('g').attr('class', 'yAxis');

        xScale.domain([0, max(data, d => Math.max(d.values[0], d.values[1]))]).range([0, width]);
        yScale.domain(data.map(item => item.statName)).range([0, height]);

        svg.select('.xAxis')
            .transition()
            .duration(500)
            .delay(1000)
            .style('opacity', '1');

        updateData(data);
    });

    function updateData(newData) {
        data = newData;

        xScale.domain([0, max(data, d => Math.max(d.values[0], d.values[1]))]).range([0, width]);
        yScale.domain(data.map(item => item.statName)).range([0, height]);

        // Add groups for all the pairs of {baseValue,adjValue}
        const slice = graph
            .selectAll('.slice')
            .data(data)
            .enter()
            .append('g')
            .attr('class', 'slice');

        slice.exit().remove();

        // Add for each of our groups, get the rectangles inside the groups and bind them to data
        const bars = graph
            .selectAll('.slice')
            .selectAll('rect')
            .data(function(d) {
                return [{ statName: d.statName, value: d.values[0] }, { statName: d.statName, value: d.values[1] }];
            });

        const text = graph
            .selectAll('.slice')
            .selectAll('text')
            .data(function(d) {
                return [{ statName: d.statName, value: d.values[0] }, { statName: d.statName, value: d.values[1] }];
            });

        // Add bar pairs for any new data groups
        bars.enter()
            .append('rect')
            .attr('x', xScale(0))
            .attr('width', 0)
            .attr('y', (d, i, n) => yScale(d.statName) - 2.5 + 13 * i)
            .attr('height', 12)
            .attr('fill', (d, i, n) => colors[i])
            // .style('filter', 'url(#glow)') glow effect
            .style('padding', '3px')
            .style('margin', '3px')

            .style('stroke-width', '0.5')
            .style('stroke', rgb(225, 225, 225))
            .attr('stroke-width', 5)
            .on('mouseover', function(d, i, n) {
                d3.select(this).style('fill', rgb(colors[i]).darker(1));
            })
            .on('mouseout', function(d, i, n) {
                d3.select(this).style('fill', colors[i]);
            })
            .transition()
            .duration(1000)
            .attr('width', d => {
                return xScale(d.value);
            });

        text.enter()
            .append('text')
            .attr('class', 'bartext')
            .attr('x', d => {
                return Math.max(0, xScale(d.value) - d.value.toString().length * 7);
            })
            .attr('y', (d, i, n) => yScale(d.statName) + 13 * i + 7.25) // 15 is distance between bars, 6 is bar height
            .attr('fill', 'black')
            .attr('text-anchor', 'right')
            .attr('font-size', '0.7rem')
            .style('opacity', '0')
            .text(d => {
                return d.value;
            })
            .transition()
            .delay(1000)
            .style('opacity', '1');

        text.exit().remove();
        bars.exit().remove();

        // Update Current
        bars.attr('y', (d, i, n) => yScale(d.statName) - 2.5 + 10 * i);
        bars.transition()
            .duration(200)
            .attr('width', d => {
                return xScale(d.value);
            });

        text.attr('x', d => {
            return Math.max(0, xScale(d.value) - d.value.toString().length * 7);
        })
            .attr('y', (d, i, n) => yScale(d.statName) - 3.5 + 10 * i + 4.5 + 5)
            .text(d => {
                return d.value;
            });

        // Call this after we create our lines or else these lines will be selected
        xAxisGroup.call(xAxis);
        yAxisGroup
            .call(yAxis)
            .select('path')
            .remove();

        yAxisGroup
            .selectAll('text')
            .attr('text-anchor', 'right')
            .attr('font-size', '0.8rem')
            .attr('transform', `translate(0, 7.5)`);
    }

    /** Update changes made to a single stat if that stat is on screen */
    window.updateStat = function(newData) {
        console.log(data);
        let i = data.findIndex(item => item.statName == newData.statName);
        if (i !== -1) {
            if ('baseValue' in newData) data[i].values[0] = newData.baseValue;
            if ('adjValue' in newData) data[i].values[1] = newData.adjValue;
            updateData(data);
        }
    };

    /** Called when we are viewing a different hero's stats or we've switched to a different view
     * like showing only vitals, showing only elemental affinities, etc.
     */
    window.updateStatInfo = function(newData) {
        updateData(newData);
    };
</script>

<main>
    <div class="container">
        <div class="canvas"></div>
    </div>
</main>

<style>
    main {
        box-sizing: border-box;
    }
</style>
